/* Define CSS variables globally first */
:root {
  /* Base colors */
  --soft-black: #353839;
  --soft-white: #f1d2b6;
  --soft-tan: #E8E0D5;
  --primary-accent: #4f7a92;
  --secondary-accent: #db4f24;

  /* UI Colors */
  --background: var(--soft-white);
  --foreground: #353839;
  
  /* Card and UI element colors */
  --card: var(--soft-white);
  --card-foreground: #353839;
  --popover: var(--soft-white);
  --popover-foreground: #353839;
  
  /* Primary colors */
  --primary: var(--primary-accent);
  --primary-foreground: #ffffff;
  
  /* Secondary colors */
  --secondary: var(--secondary-accent);
  --secondary-foreground: #ffffff;
  
  /* Utility colors */
  --muted: #E2E2E2;
  --muted-foreground: #353839;
  --accent: var(--primary-accent);
  --accent-foreground: #ffffff;
  --destructive: #D32F2F;
  --destructive-foreground: #ffffff;
  
  /* Border and utility colors */
  --border: var(--primary-accent);
  --input: #E0E0E0;
  --ring: var(--primary-accent);
  
  /* Border radius */
  --radius: 0.5rem;
}

.dark {
  /* Base colors in dark mode */
  --background: #1a1a1a;
  --foreground: #f1d2b6;
  
  /* Card and UI element colors in dark mode */
  --card: #1E1E1E;
  --card-foreground: #f1d2b6;
  --popover: #1E1E1E;
  --popover-foreground: #f1d2b6;
  
  /* Primary colors in dark mode */
  --primary: var(--primary-accent);
  --primary-foreground: #ffffff;
  
  /* Secondary colors in dark mode */
  --secondary: var(--secondary-accent);
  --secondary-foreground: #ffffff;
  
  /* Utility colors in dark mode */
  --muted: #2A2A2A;
  --muted-foreground: #f1d2b6;
  --accent: var(--primary-accent);
  --accent-foreground: #ffffff;
  --destructive: #F44336;
  --destructive-foreground: #ffffff;
  
  /* Border and utility colors in dark mode */
  --border: var(--primary-accent);
  --input: #333333;
  --ring: var(--primary-accent);
}

